import * as React from 'react';
import {QAndAComponent} from './q-and-a/q-and-a-component';
import {Contribution} from '../../../../model/contribution';
import {RoutingPass} from '../../../../constant/routing-pass';
import {ContributionConfirmComponent} from './contribution-confirm/contribution-confirm-component';
import minEkiKun from 'src/assets/min-eki-kun.png';
import {NavigationBarComponent} from '../../../common/navigation-bar-component/navigation-bar-component';
import './page-contribution-post-component.scss';
import {CorrespondStatus} from '../../../../enum/correspond-status';

interface PageWebPostContributionComponentProps {
  history: any;
  isFromList: boolean;
}

interface PageWebPostContributionComponentState {
  route: string | null;
  station: string | null;
  site: string | null;
  facility: string | null;
  category: string | null;
  thumbnail: string | null;
  remarks: string | null;
  contribution: Contribution | null;
}

export class PageContributionPostComponent extends React.Component<PageWebPostContributionComponentProps, PageWebPostContributionComponentState> {

  private questionAndAnswerList: QuestionAndAnswer[];
  private currentQuestion: QuestionTitle = QuestionTitle.route;

  constructor(props: PageWebPostContributionComponentProps) {
    super(props);
    this.state = {
      route: null,
      station: null,
      site: null,
      facility: null,
      category: null,
      thumbnail: null,
      remarks: null,
      contribution: null,
    };

    this.questionAndAnswerList = [
      new QuestionAndAnswer(
        '不具合を見つけた路線を教えてください。',
        this.state.route,
        QuestionTitle.route,
        '路線',
        (value: string) => this.handlerClick(QuestionTitle.route, value),
      ),
      new QuestionAndAnswer(
        '不具合を見つけた駅を教えてください。',
        this.state.station,
        QuestionTitle.station,
        '駅',
        (value: string) => this.handlerClick(QuestionTitle.station, value),
      ),
      new QuestionAndAnswer(
        '駅のどこで不具合を見つけましたか？',
        this.state.site,
        QuestionTitle.site,
        '場所',
        (value: string) => this.handlerClick(QuestionTitle.site, value),
      ),
      new QuestionAndAnswer(
        '何に不具合が起きていますか？',
        this.state.facility,
        QuestionTitle.facility,
        '設備',
        (value: string) => this.handlerClick(QuestionTitle.facility, value),
      ),
      new QuestionAndAnswer(
        'どんな不具合が起きていますか？',
        this.state.category,
        QuestionTitle.category,
        'カテゴリ',
        (value: string) => this.handlerClick(QuestionTitle.category, value),
      ),
      new QuestionAndAnswer(
        '不具合が起きている場所の写真をアップしてください。',
        this.state.thumbnail,
        QuestionTitle.thumbnail,
        '写真',
        (value: string) => this.handlerClick(QuestionTitle.thumbnail, value),
      ),
      new QuestionAndAnswer(
        '他に何かありますか？',
        this.state.remarks,
        QuestionTitle.remarks,
        '備考',
        (value: string) => this.handlerClick(QuestionTitle.remarks, value),
      ),
    ];
  }

  render() {
    let isDisplay: boolean = true;
    const contribution = this.state.contribution;
    const isFromList = this.props.isFromList;
    return (
      <div className={'post_wrap'}>

        <div className={'header_wrap post_head'}>
          <NavigationBarComponent
            callback={() => this.props['history'].push(isFromList ? RoutingPass.MY_CONTRIBUTION : RoutingPass.HOME)}
            pageTitle={'投稿する'}
          />
        </div>

        <div className={'body_area post_body'}>
          <div className={'bodyInner_wrap'}>

        {this.questionAndAnswerList.map((data, i) => (
          <div key={i} className={'talk_wrap'}>
            {isDisplay ? (
              <div>
                {isDisplay = this.currentQuestion !== data.questionTitle}
                <QAndAComponent
                  isInput={!isDisplay}
                  message={data.message}
                  value={data.value || ''}
                  property={data.questionTitle}
                  callbackClick={data.callbackClick}
                />
              </div>
            ) : (<></>)}
          </div>
        ))}

        {this.currentQuestion === QuestionTitle.confirm ? contribution ? (
          <div>
            <div>
              この内容で報告
            </div>
            <div>

              <div>
                <img
                  src={minEkiKun}
                />
              </div>

              <div>
                投稿が完了しました。
              </div>
              <div>
                <div>
                  <button
                    onClick={() => this.resetState()}
                  >
                    続けて投稿
                  </button>
                </div>

                <div>
                  <button
                    onClick={() => this.endContribution()}
                  >
                    投稿を終了
                  </button>
                </div>
              </div>
            </div>

          </div>
        ) : (
          <ContributionConfirmComponent
            answerList={this.questionAndAnswerList}
            callbackOK={() => this.callbackClickBtnOK()}
            callbackAgain={() => this.callbackClickBtnAgain()}
          />
        ) : (<></>)}

          </div>
        </div>

      </div>
    );
  }


  private handlerClick(question: QuestionTitle, value: string): void {
    this.questionAndAnswerList[question].reloadValue = value;
    if (value) {
      this.currentQuestion++;
    }
    switch (question) {
      case QuestionTitle.route:
        this.setState({
          route: value,
        });
        break;
      case QuestionTitle.station:
        this.setState({
          station: value,
        });
        break;
      case QuestionTitle.site:
        this.setState({
          site: value,
        });
        break;
      case QuestionTitle.facility:
        this.setState({
          facility: value,
        });
        break;
      case QuestionTitle.category:
        this.setState({
          category: value,
        });
        break;
      case QuestionTitle.thumbnail:
        this.setState({
          thumbnail: value,
        });
        break;
      case QuestionTitle.remarks:
        if (!value) {
          this.currentQuestion++;
        }
        this.questionAndAnswerList[question].reloadValue = this.questionAndAnswerList[question].value;
        this.setState({
          remarks: value,
        });
        break;
    }
  }

  private callbackClickBtnOK(): void {
    this.registerContribution();
  }

  private callbackClickBtnAgain(): void {
    this.currentQuestion = QuestionTitle.route;
    this.setState({
      contribution: null,
    });
  }

  private resetState(): void {
    this.questionAndAnswerList.map((data) => data.value = null);
    this.questionAndAnswerList[QuestionTitle.route].value = '';
    this.currentQuestion = QuestionTitle.route;
    this.setState({
      contribution: null,
    });
  }

  private registerContribution(): void {
    const route = this.state.route || '';
    const station = this.state.station || '';
    const site = this.state.site || '';
    const facility = this.state.facility || '';
    const category = this.state.category || '';
    const thumbnail = this.state.thumbnail || '';
    const remarks = this.state.remarks || '';

    this.setState({
      contribution: new Contribution(
        0,
        route,
        station,
        site,
        facility,
        category,
        thumbnail,
        remarks,
        CorrespondStatus.NotYet,
      ),
    });
  }

  private endContribution(): void {
    this.props['history'].push(RoutingPass.HOME);
  }
}

export enum QuestionTitle {
  route,
  station,
  site,
  facility,
  category,
  thumbnail,
  remarks,
  confirm,
}

export class QuestionAndAnswer {
  constructor(
    public message: string,
    public value: string | null,
    public questionTitle: QuestionTitle,
    public questionTitleValue: string,
    public callbackClick: (value: string) => void,
  ) {
  }

  public set reloadValue(value: string | null) {
    this.value = value;
  }
}



