import * as React from 'react';
import {DebugService} from "../../../service/debug-service";
import {RoutingPass} from '../../../constant/routing-pass';
import {ColorGenerator} from '../../../util/color-generator';

interface DebugComponentProps {
}

interface DebugComponentState {
  debugList: DebugObj[];
}

export class DebugComponent extends React.Component<DebugComponentProps, DebugComponentState> {

  constructor(props: DebugComponentProps) {
    super(props);
    this.state = {
      debugList: [],
    };
  }

  render() {
    return (
      <div
        className={'debug_wrap'}
        style={{backgroundColor: ColorGenerator.createRandomRGBA(0.2)}}
      >
        Debug<br/>
        {this.state.debugList.map((dbg, i) => (
          <button
            key={dbg.label + "_" + i}
            onClick={dbg.callback}
          >
            {dbg.label}
          </button>))}
      </div>
    );
  }

  componentDidMount(): void {
    DebugService.ins.initialize(this);
  }
}

export class DebugObj {

  constructor(
    public callback: () => void,
    public label: string,
  ) {
  }

}



