import * as React from 'react';
import {QuestionAndAnswer, QuestionTitle} from '../page-contribution-post-component';
import minEkiKun from 'src/assets/min-eki-kun.png';

interface ConfirmContributionInfoComponentProps {
  answerList: QuestionAndAnswer[];
  callbackOK: () => void;
  callbackAgain: () => void;
}

interface ConfirmContributionInfoComponentState {
}

export class ContributionConfirmComponent extends React.Component<ConfirmContributionInfoComponentProps, ConfirmContributionInfoComponentState> {

  constructor(props: ConfirmContributionInfoComponentProps) {
    super(props);
  }

  render() {
    const answerList = this.props.answerList;
    return (
      <div>
        <div>
          <img
            src={minEkiKun}
          />
        </div>

        <div>
          報告内容の確認をお願いします。
        </div>

        {answerList.map((answer, i) => (
          <div key={i}>
            {answer.questionTitle === QuestionTitle.thumbnail ? (
              <img
                src={answer.value || ''}
              />
            ) : (
              <div>
                {answer.questionTitleValue}
                {answer.value}
              </div>
            )}
          </div>
        ))}

        <div>
          <div>
            <button
              onClick={() => this.props.callbackAgain()}
            >
              やり直し
            </button>
          </div>

          <div>
            <button
              onClick={() => this.props.callbackOK()}
            >
              この内容で報告
            </button>
          </div>
        </div>
      </div>
    );
  }

}



