export class ColorGenerator {

  static createStr(numR: number, numG: number, numB: number): string {
    const strR: string = numR.toString(16).length === 1 ? '0' + numR.toString(16) : numR.toString(16);
    const strG: string = numG.toString(16).length === 1 ? '0' + numG.toString(16) : numG.toString(16);
    const strB: string = numB.toString(16).length === 1 ? '0' + numB.toString(16) : numB.toString(16);
    return '#' + strR + strG + strB;
  }

  static createStrRGBA(numR: number, numG: number, numB: number, alpha: number): string {
    return `rgba(${numR},${numG},${numB},${alpha})`;
  }

  static createRandomStr(): string {
    const numR: number = Math.floor( Math.random() * 16 * 16);
    const numG: number = Math.floor( Math.random() * 16 * 16);
    const numB: number = Math.floor( Math.random() * 16 * 16);
    return this.createStr(numR, numG, numB);
  }

  static createRandomRGBA(alpha: number): string {
    const numR: number = Math.floor( Math.random() * 16 * 16);
    const numG: number = Math.floor( Math.random() * 16 * 16);
    const numB: number = Math.floor( Math.random() * 16 * 16);
    return this.createStrRGBA(numR, numG, numB, alpha);
  }

}



