import * as React from 'react';
import {QuestionTitle} from '../page-contribution-post-component';
import './q-and-a-component.scss';
import minEkiKun from '../../../../../assets/image/common/min-eki-kun.png';

interface QAndAComponentProps {
  isInput: boolean;
  message: string;
  value: string;
  property: QuestionTitle;
  callbackClick: (value: string) => void;
}

interface QAndAComponentState {
  value: string;
}

export class QAndAComponent extends React.Component<QAndAComponentProps, QAndAComponentState> {
  private imgUrl: string = '';

  constructor(props: QAndAComponentProps) {
    super(props);
    this.state = {
      value: props.value,
    };
  }

  componentWillReceiveProps(nextProps: Readonly<QAndAComponentProps>, nextContext: any): void {
    this.setState({
      value: nextProps.value,
    });
  }

  render() {
    const message = this.props.message;
    const value = this.state.value;
    const property = this.props.property;
    const isImage = this.props.property === QuestionTitle.thumbnail;
    return (
      <div className={'q_and_a_wrap'}>


        {/* TODO 投稿確認用 */}
        <div className={'question_wrap flex_box'}>
          <div className={'thumb_wrap'}>
            <img
              src={minEkiKun}
            />
          </div>

          <div className={'message_wrap'}>
            <div className={'messageInner_wrap'}>
              {message}
            </div>
          </div>
        </div>

        {/* TODO 選択肢だす用 */}
        {/*
        <div className={'answer_wrap selectList_wrap'}>
          <div className={'text_wrap'}>
            <div className={'textInner_wrap'}>
              <div className={'selectListItem'}>hoge</div>
              <div className={'selectListItem'}>hoge</div>
              <div className={'selectListItem'}>hoge</div>
              <div className={'selectListItem'}>hoge</div>
              <div className={'selectListItem'}>hoge</div>
              <div className={'selectListItem'}>hoge</div>
              <div className={'selectListItem'}>hoge</div>
              <div className={'selectListItem'}>hoge</div>
              <div className={'selectListItem'}>hoge</div>
              <div className={'selectListItem'}>hoge</div>
              <div className={'selectListItem'}>hogehogehogehogehogehogehogehogehogehogehogehogehogehogehogehogehogehoge</div>
              <div className={'selectListItem'}>hoge</div>
              <div className={'selectListItem'}>hoge</div>
            </div>
          </div>
        </div>
        */}

        {this.props.isInput ? (
          <div
            className={isImage ? 'imagePost_wrap' : 'bottom_post_wrap flex_box'}
            // TODO 画像投稿時は"imagePost_wrap"のクラスを付与してください
          >

            {isImage ? (
              <div className={'input_wrap'}>
                {/* TODO 画像投稿の時はこっちを使う */}
                {/*/!* TODO 画像を選択した時(※送信推す前)に選択した画像のサムネを表示するエリア *!/*/}
                <div className={'selectImageThumb_wrap'}>
                  {/*<img*/}
                    {/*src={minEkiKun}*/}
                  {/*/>*/}
                  <button type={'button'} className={'reset'}>
                    ×
                  </button>
                </div>

                <label className={'selectFileInput_wrap'} form={'selectFile'}>
                  <span className={'flex_box flex_align_center flex_content_center'}>
                    <img
                      src={value || minEkiKun}
                      onClick={() => this.handlerClickTakeImg()}
                    />
                  </span>
                  {/*<span className={'flex_box flex_align_center flex_content_center'}><img src={iconCamera}/></span>*/}
                  <input
                    id={'selectFile'}
                    type={'file'}
                  />
                </label>
              </div>
            ) : (
              <div className={'input_wrap'}>
                {/* TODO 通常の回答はこっち */}
                <input
                  onChange={(e) => this.setState({value: e.target.value})}
                  value={value}
                />
              </div>
            )}

            <div className={'btn_wrap'}>
              <button
                className={'reset btnStyle_1'}
                onClick={() => isImage ? this.setThumbnailState() : this.handlerClick()}
              >
                送信
              </button>
            </div>
          </div>
        ) : (
          <div className={'answer_wrap'}>
            {isImage ? (
              <div>
                <img
                  src={value}
                />
              </div>
            ) : (
              <>
              { value ? (
                <div className={'text_wrap'}>
                  {value}
                </div>
              ) : (
                <></>
              )}
              </>
            )}
          </div>
        )}

      </div>
    );
  }

  private setThumbnailState(): void {
    if(this.imgUrl){
      this.props.callbackClick(this.imgUrl);
    }else{
      this.props.callbackClick('hoge');
    }
  }

  private handlerClick(): void {
    this.props.callbackClick(this.state.value);
    this.setState({
      value: '',
    });
  }

  private handlerClickTakeImg(): void {
    const inputElement: HTMLInputElement = document.createElement('input');
    inputElement.type = 'file';
    inputElement.accept = 'image/png, image/jpeg';
    inputElement.onchange = (e: any) => {
      this.readImgFile((e.target).files[0]);
    }
    inputElement.value = '';
    inputElement.onclick = () => {
      document.body.removeChild(inputElement);
    }
    document.body.appendChild(inputElement);
    inputElement.click();
  }

  private readImgFile(file: File): void {
    const reader: FileReader = new FileReader();
    reader.readAsDataURL(file);
    reader.onload = () => {
        if (reader.error) {
          console.error(reader.error);
          return;
        }
    }
    this.setState({
      value: reader.result as string,
    });
  }

}



