import {DebugComponent, DebugObj} from "../component/common/debug-component/debug-component";

export class DebugService {
  debugComponent: DebugComponent;
  private static _ins: DebugService;

  private constructor() {
  }

  static get ins(): DebugService {
    if (!this._ins) {
      this._ins = new DebugService();
    }
    return this._ins;
  }

  initialize(DebugComponent: DebugComponent): void {
    this.debugComponent = DebugComponent;
  }

  setDebug(callback: () => void, label: string): void {
    const arr: DebugObj[] = this.debugComponent.state.debugList;
    arr.push(new DebugObj(callback, label));
    this.debugComponent.setState({
      debugList: arr
    });
  }
  
}



