import * as React from 'react';
import {QueryParamFormatter} from '../../../../util/query-param-formatter';
import {RoutingPass} from '../../../../constant/routing-pass';
import minEkiKun from 'src/assets/min-eki-kun.png';
import {NavigationBarComponent} from '../../../common/navigation-bar-component/navigation-bar-component';
import './page-contribution-list-component.scss';
import iconArrowRight from '../../../../assets/image/common/icon_arrow_right.svg';
import {CorrespondStatus} from '../../../../enum/correspond-status';
import {CorrespondStatusCheckServie} from '../../../../service/correspond-status-check-servie';

interface PageWebContributionListComponentProps {
  history: any;
  isAll: boolean;
}

interface PageWebContributionListComponentState {
  isLoading: boolean;
  contributionList: IContribution[];
}

export class PageContributionListComponent extends React.Component<PageWebContributionListComponentProps, PageWebContributionListComponentState> {
  private route: string = '';
  private station: string = '';
  private status: CorrespondStatus = CorrespondStatus.NotYet;

  constructor(props: PageWebContributionListComponentProps) {
    super(props);
    this.state = {
      isLoading: true,
      contributionList: [],
    };
    this.parse();
  }

  componentWillMount(): void {
    setTimeout(() =>
      this.setState({
        isLoading: false,
        contributionList: [
          {
            img: minEkiKun,
            status: CorrespondStatus.NotYet,
            station: 'hoge',
            date: '2019/01/01',
          },
          {
            img: minEkiKun,
            status: CorrespondStatus.During,
            station: 'hoge',
            date: '2019/01/02',
          },
          {
            img: minEkiKun,
            status: CorrespondStatus.Complete,
            station: 'hoge',
            date: '2019/01/03',
          },
        ],
      }), 2000);
  }

  render() {
    const isAll = this.props.isAll;
    const list = this.state.contributionList;
    const isLoading = this.state.isLoading;
    const route = this.route;
    const station = this.station;
    const status = this.status;
    return (
      <div>

        <div className={'header_wrap pastPostList'}>{/* TODO みんなの投稿一覧の時は"pastPostList"のクラスを"postListAll"のクラスに変更 */}
          <NavigationBarComponent
            callback={() => this.props['history'].push(isAll ? RoutingPass.REFINE_ALL_CONTRIBUTION : RoutingPass.HOME)}
            pageTitle={isAll ? 'みんなの投稿一覧' : '過去投稿一覧'}
          />
        </div>

        <div className={'body_area pastPostList_body'}>

          <>
            {isAll ? (
              <div className={'postListAll_head'}>
                {route} : {station} : {CorrespondStatusCheckServie.ins.returnCorrespondStatusAsString(status)} <span>の検索結果</span>
              </div>
            ) : (
              <>
              </>
            )}
          </>

          {isLoading ? (
            <div className={'loading_wrap flex_box flex_align_center flex_content_center'}>
              <div className={'loader'}>LOADING...</div>
            </div>
          ) : (
            list.map((data, i) => (
              <div
                className={'listStyle_wrap pastPostList'}
                onClick={() => this.handlerClickGotoDetail()}
                key={'_' + i}
              >

                <div className={'listItem_wrap flex_box flex_align_center'}>
                  <div className={'listItem_left flex_box'}>
                    <div className={'innerLeft'}>
                      <img src={data.img}/>
                    </div>
                    <div className={'innerRight'}>
                      <div className={'itemStatus'}>
                        {data.status === CorrespondStatus.NotYet ? (
                          <span className={'unsupported'}>
                            {CorrespondStatusCheckServie.ins.returnCorrespondStatusAsString(data.status)}
                          </span>
                        ) : data.status === CorrespondStatus.During ? (
                          <span className={'responding'}>
                            {CorrespondStatusCheckServie.ins.returnCorrespondStatusAsString(data.status)}
                          </span>
                        ) : (
                          <span className={'complete'}>
                            {CorrespondStatusCheckServie.ins.returnCorrespondStatusAsString(data.status)}
                          </span>
                        )}
                        {/* TODO ステータスによって以下のようにクラス名を付与してください */}
                        {/*<span className={'unsupported'}>未対応</span>*/}
                        {/*
                        <span className={'responding'}>対応中</span>
                        <span className={'complete'}>完了</span>
                        */}
                      </div>

                      <div className={'divide5'} />

                      <div className={'stationName'}>
                        {this.station}駅
                      </div>

                      <div className={'itemDate'}>
                        {data.date}
                      </div>
                    </div>
                  </div>
                  <div className={'listItem_right'}>
                    <img src={iconArrowRight} />
                  </div>
                </div>

              </div>
            ))
          )}

          <div className={'divide30'} />

          <div className={'btn_wrap'}>
            {isAll ? (
              <></>
            ) : (
              <button
                className={'reset btnStyle_1'}
                onClick={() => this.handlerClickGotoContribution()}
              >
                投稿する
              </button>
            )}
          </div>
        </div>

      </div>
    );
  }

  private parse(): void {
    const parsed = QueryParamFormatter.queryParse(location.href);
    this.route = parsed.route;
    this.station = parsed.station;
    this.status = CorrespondStatusCheckServie.ins.returnCorrespondStatusAsEnum(parsed.status);
  }

  private handlerClickGotoContribution(): void {
    this.props['history'].push(RoutingPass.POST_CONTRIBUTION_FROM_LIST);
  }

  private handlerClickGotoDetail(): void {
    const query = QueryParamFormatter.queryCompose({route: this.route, station: this.station, status: this.status});
    this.props['history'].push(this.props.isAll ? RoutingPass.ALL_CONTRIBUTION_DETAIL + '?' + query : RoutingPass.CONTRIBUTION_DETAIL);
  }

}

interface IContribution {
  img: string;
  status: CorrespondStatus;
  station: string;
  date: string;
}



