import * as React from 'react';
import {RoutingPass} from '../../../../constant/routing-pass';
import './page-login-component.scss';
import loginLogo from '../../../../assets/image/login/login_logo.svg';
import loginWhite from '../../../../assets/image/login/white.png';
import loginline from '../../../../assets/image/login/line.svg';

interface PageWebLoginComponentProps {
}

interface PageWebLoginComponentState {
  email: string;
  password: string;
}

export class PageLoginComponent extends React.Component<PageWebLoginComponentProps, PageWebLoginComponentState> {

  constructor(props: PageWebLoginComponentProps) {
    super(props);
    this.state = {
      email: '',
      password: '',
    }
  }

  render() {
    const email = this.state.email;
    const password = this.state.password;
    return (
      <div className={'login_wrap'}>

        <div className={'login_head'}> {/* ロゴ */}
          <img className={'login_head_logo'} src={loginLogo} alt="みんなの駅" />
          <img className={'login_head_white'} src={loginWhite} alt="みんなの駅" />
        </div>

        <div className={'body_area login_body'}> {/* body */}

          <div>
            <input
              className={'textInput'}
              onChange={(e) => this.setState({email: e.target.value})}
              value={email}
              placeholder={'メールアドレス'}
            />

            <div className={'divide20'} />

            <input
              className={'textInput'}
              type={'password'}
              onChange={(e) => this.setState({password: e.target.value})}
              value={password}
              placeholder={'パスワード'}
            />
          </div>

          <div className={'divide20'} />

          <button className={'reset btnStyle_1'} onClick={() => this.checkLogin()}>ログイン</button>

          <div className={'divide20'} />

          <div className={'pass_forget'}>パスワードをお忘れの時は、<a href={"http://hoge.co.jp"}>パスワード再設定手続き</a>をご利用ください。</div>

          <div className={'divide30'} />

          <div className={'line_wrap'}>
            <img src={loginline} />
          </div>

          <div className={'divide30'} />

          <div>ご利用がはじめての方は新規ユーザー登録をしてください。</div>

          <div className={'divide20'} />

          <div>
            <button className={'reset btnStyle_1'} onClick={() => console.log('new regist')}>新規ユーザー登録</button>
          </div>

        </div>

      </div>
    );
  }

  private checkLogin(): void {
    this.props['history'].push(RoutingPass.HOME);
  }

}



